<template>
  <div style="display: flex">
    <h2 class="title" style="flex: 9">牧马南山后端试题</h2>
    <div style="display: inline-block;flex: 1;margin-top: 3px">
      <el-button type="danger" icon="el-icon-lollipop" circle plain size="small" @click="tip"></el-button>
    </div>
  </div>

  <div>对web感兴趣吗？加入我们吧！</div>
  <div>
    <el-card shadow="hover" class="login-form">
      <el-form action="login">
        <el-form-item>
          <el-row :gutter="10">
            <el-col :xs="10" :sm="10" :md="12" :lg="15" :xl="10">
              <h2 style="text-align: left;font-weight: 300">登录</h2>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item>
          <el-row :gutter="10">
            <el-col :xs="10" :sm="10" :md="12" :lg="15" :xl="10">
              <el-input prefix-icon="el-icon-user" placeholder="账号" v-model="formData.userName"
                        name="userName"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item>
          <el-row :gutter="10">
            <el-col :xs="10" :sm="10" :md="12" :lg="15" :xl="10">
              <el-input prefix-icon="el-icon-key" placeholder="密码" v-model="formData.passWord" name="passWord"
                        type="password"></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item>
          <el-row :gutter="10">
            <el-col :xs="10" :sm="10" :md="12" :lg="15" :xl="10">
              <el-input prefix-icon="el-icon-position" placeholder="接口地址" v-model="address"></el-input>
              <el-radio-group v-model="method" size="mini">
                <el-radio-button label="post"></el-radio-button>
                <el-radio-button label="get"></el-radio-button>
              </el-radio-group>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item>
          <el-row :gutter="10">
            <el-col :xs="4" :sm="6" :md="8" :lg="9" :xl="11">
              <el-button class="login-btn" @click="login" type="primary" plain :loading="btnLoading">登 录</el-button>
            </el-col>
          </el-row>
        </el-form-item>
      </el-form>
    </el-card>
    <el-divider content-position="left" class="divider">大致步骤</el-divider>
    <el-card shadow="hover" style="text-align: left;padding:0 20px;width: 90%">
      <el-steps>
        <el-step title="完成后端登录接口编写" description="编程语言不限"></el-step>
        <el-step title="填写内容" status="process"
                 description="填写你在后端编写登录的接口地址，接受的参数名为userName与passWord(注意区分大小写)，post与get为你实现的接口的提交方式，默认为post"></el-step>
        <el-step title="点击登录" status="process" description="进行调试，若你实现的接口地址填写是正确的，则在下面的调试信息中会有内容输出"></el-step>
      </el-steps>
    </el-card>
    <el-divider content-position="left" class="divider">调试信息</el-divider>
    <el-card shadow="hover" id="debug" style="text-align: left;padding:0 20px;width: 90%;">
      <json-viewer :value="jsonData"/>
    </el-card>
  </div>
  <div></div>
  <div class="footer">
    <span>Copyright©2020-2021 </span>
    <a href="https://github.com/class-undefined" class="copyright" target="_blank">野漫横江</a>
    <span> All Rights Reserved.</span>
  </div>
</template>

<script>
import {ref, reactive, h} from 'vue'
import request, {address} from "@/utils/request";
import {ElLoading, ElMessageBox, ElNotification} from 'element-plus'

export default {
  name: 'App',
  components: {},
  setup() {
    const formData = reactive({userName: '', passWord: ''})
    const jsonData = ref({})
    const method = ref('post')
    const params = {
      url: address.value,
      method: method.value
    }
    const btnLoading = ref(false)
    if (method.value === 'get') {
      params.params = formData
    }
    if (method.value === 'post') {
      params.data = formData
    }
    const login = () => {
      const loading = ElLoading.service({target: '#debug'})
      btnLoading.value = true
      params.url = address.value
      request(params).then(r => {
        jsonData.value = r
        loading.close
        btnLoading.value = false
      }).catch(r => {
        jsonData.value = r
        loading.close()
        btnLoading.value = false
        ElNotification({
          type: 'error',
          title: '发送失败',
          message: '请检查接口地址是否填写正确，或后端接口是否已进行跨域处理。'
        })
      })
      console.log(formData);
    }
    const tip = () => {
      ElMessageBox({
        title: '提示',
        message: h('p', {style: {fontWeight: 100}}, [
          h('span', null, '调试过程中你可能会遇到'),
          h('a', {href: 'https://www.zhihu.com/question/26376773', target: '_blank', class: 'cors'}, '跨域问题'),
          h('div', null, '请自行解决。( 这也是一个附加能力的考察 )'),

        ])
      })
    }
    return {
      formData, login, address, jsonData, method, btnLoading, tip
    }
  }
}
</script>

